"use client"

import { useState } from "react"
import CreditCard from "@/components/credit-card"
import PaymentForm from "@/components/payment-form"
import { useRouter } from "next/navigation"

export default function PaymentPage() {
  const [cardData, setCardData] = useState({
    cardNumber: "",
    cardHolder: "",
    expiryDate: "",
    cvv: "",
    cardType: "visa" as "visa" | "mastercard" | "amex" | "discover" | "diners" | "jcb",
  })

  const [isFlipped, setIsFlipped] = useState(false)

  return (
    <main className="min-h-screen bg-white from-background via-muted/20 to-background flex items-center justify-center p-4 sm:p-6">
      <div className="w-full max-w-md">
        <div className="mb-8 text-center space-y-3">
          <h1 className="text-5xl font-bold bg-gradient-to-r from-primary to-accent bg-clip-text text-transparent">
            Paiement mobile
          </h1>
       
         
        </div>

        <div className="mb-10 flex justify-center perspective">
          <CreditCard
            cardNumber={cardData.cardNumber}
            cardHolder={cardData.cardHolder}
            expiryDate={cardData.expiryDate}
            cvv={cardData.cvv}
            cardType={cardData.cardType}
            isFlipped={isFlipped}
          />
        </div>

        <PaymentForm cardData={cardData} setCardData={setCardData} isFlipped={isFlipped} setIsFlipped={setIsFlipped} />
      </div>
    </main>
  )
}
